<template>
	<div id="comment">
		<common-header :title="'用户评价'"></common-header>
		
		<div class="header">
			<div :class="{active:1==tabIndex}" @click="changeRang(1)">
				<div>全部评价</div>
				<div class="num">2560</div>
			</div>
			<div :class="{active:2==tabIndex}" @click="changeRang(2)">
				<div>好评</div>
				<div class="num">2560</div>
			</div>
			<div :class="{active:3==tabIndex}" @click="changeRang(3)">
				<div>中评</div>
				<div class="num">2560</div>
			</div>
			<div :class="{active:4==tabIndex}" @click="changeRang(4)">
				<div>差评</div>
				<div class="num">2560</div>
			</div>
			<div :class="{active:5==tabIndex}" @click="changeRang(5)">
				<div>晒图</div>
				<div class="num">2560</div>
			</div>
		</div>
		
		<template v-if="comments.length>0">
		<div class="comment-list"
			v-infinite-scroll="loadMore"
		  infinite-scroll-disabled="loading"
		  infinite-scroll-distance="10"
			>
			<!-- 列表项 -->
			<div class="list-wrap" v-for="(item,index) in comments">
				<div class="list-item clearfix">
					<div class="item-img fl-left">
						<img :src="item.facePath" alt="">
					</div>
					<div class="item-info fl-left">
						<h4 class="text-over">{{item.name}}</h4>
						<p>{{item.comTime}}</p>
						<div class="start_sel">
							<div class="star" v-bind:class="'star-'+ parseInt(item.starNum)"></div>
						</div>
					</div>
				</div>
				<p class="comment-desc">{{item.content}}</p>
				<div class="comment-imgs clearfix" v-if="item.images.length > 0">
					<img class="img-item" :src="img.imagePath" alt="" v-for="(img,index) in item.images"/>
				</div>
			</div>
		</div>
			
			</template>
			
			<template  v-else>
	      <div class="remind-text">
	        {{loadingText?'加载中...':'暂无相关数据'}}
	      </div>
	    </template>

	</div>
</template>

<script>
	import CommonHeader from '@/components/Header/CommonHeader'
	import { ImagePreview } from 'vant';
	export default {
		data() {
			return {
	      isLoading: true,
        loadingText: false,

				isEmpty:false,
				tabIndex:1,
				comments:[{id:1,name:'评论人',facePath:'/static/img/pic.603b63b.png',content:'内容',comTime:'2018-01-26',images:[{id:1,imagePath:'/static/img/pic.603b63b.png'}],starNum:5}],
				starType:0,
				imageType:0,
				pa:1,
				li:10,
				ob:1,
				ot:1,
			}
		},
		created(){
			this.getCommentList()
		},
		methods:{
			
			changeRang(index){
				this.tabIndex = index
				this.pa = 1
				this.starType = index-1
				this.imageType = 0
	      this.isLoading = true

				if(index==5){
					this.starType = 0
					this.imageType = 1
				}
				this.getCommentList()
			},
			preview(){
				ImagePreview([
				  '/static/img/pic.603b63b.png',
				]);
			},
			loadMore() {
	      if(this.isLoading)
	        return;
	      this.isLoading  = true;
	      this.pa += 1;
	      this.getCommentList();
	    },
			getCommentList(){
				let id = this.$route.params.id
				let starType = this.starType
				let imageType = this.imageType
				let table = {
					pa : this.pa,
					li : this.li,
					ob : this.ob,
					ot : this.ot
				}
	      this.loadingText = true;

				this.$Api.getCommentList(id,starType,imageType,table).then((res)=>{
					if(res.q.s==0){
	          this.loadingText = false;
	          var comments = this.comments;
	          comments.push(...res.q.comments);
	          this.comments = comments;
						if(res.q.total > this.pa*this.li){
	            this.isLoading = false;
	          }else{
	            this.isLoading = true;
	          }
					}
				})
			},
		},
		components: {
			CommonHeader
		},

	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#comment {
		.header {
			position: fixed;
			display: flex;
			font-size: 0.37rem;
			top: 1.17rem;
			width: 100%;
			height: 1.87rem;
			padding: 0.43rem 0;
			z-index: 999;
			border-bottom: 1px solid $border-color;
			color: #666;
			background: #fff;
			>div {
				flex: 1;
				height: 1.01rem;
				line-height: 1;
				text-align: center;
				.num {
					padding-top: 0.29rem;
				}
			}
			.active {
				color: $label-color;
			}
		}
		.comment-list {
			position: absolute;
			top: 1.87+1.17rem;
			width: 100%;
			.list-wrap{
				border-bottom: 1px solid $border-color;
			}
			.list-item {
				display: flex;
				width: 100%;
				height: box(180);
				padding: box(30);
				.item-img {
					width: box(120);
					height: box(120);
					img {
						width: box(120);
						height: box(120);
						border-radius: box(60);
					}
				}
				.item-info {
					/*width: box(540);*/
					flex:1;
					height: box(120);
					position: relative;
					padding: box(15) 0;
					margin-left: box(30);
					color: #323232;
					h4 {
						font-size: box(32);
						width: box(240);
						overflow: hidden;
						text-overflow: ellipsis;
					}
					p {
						font-size: box(28);
						position: absolute;
						bottom: 0;
						left: 0;
						color: #666;
					}
					.start_sel {
						width: box(176);
						// height: box(26);
						overflow: hidden;
						position: absolute;
						top: box(30);
						right: 0;
					}
					.star {
						display: inline-block;
						vertical-align: initial;
						width: box(176);
						height: box(26);
						background: url("../../assets/star.png") no-repeat;
						background-size: cover;
					}
				}
			}
			.comment-desc {
				font-size: box(28);
				color: #323232;
				padding: 0 box(30) box(22) box(30);
			}
			.comment-imgs{
				width: 100%;
				padding-left:0.4rem;
			}
			.img-item{
				float: left;
				width: 2.8rem;
				height: 2.8rem;
				margin-right: 0.4rem;
				margin-bottom: 0.4rem;

			}
		}
	}
</style>


